body {
    font-family: 'Playfair Display', ;
}

.container {
    max-width: 1160px;
    width: 100%;
    margin: 0 auto;

    &-small {
        text-align: center;
        max-width: 960px;
    }
}

.button {
    &-one {
        background: linear-gradient(90deg, #1F4E78 0%, #0677DF 100%);
        box-shadow: 0px 10px 30px #C7DCEE;
        border-radius: 50px;
        font-family: 'Arimo', ;
        font-weight: bold;
        font-size: 14px;
        padding: 25px 20px;
        color: #FFFFFF;
        display: block;
    }
}

.img-fluid {
    width: 100%;
}

.slick-next,
.slick-prev {
    width: 64px;
    height: 64px;
    position: relative;
    box-shadow: 0px 20px 30px rgba(0, 0, 0, 0.2);
    border-radius: 50%;
}

.slick-next {
    background: white;
    position: absolute;
    top: 50%;
    margin-top: -32px;
    z-index: 10;
    right: 470px;

    &::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        background: url(/img/slider/arrowLeft.png) center center no-repeat;
        top: 0;
        left: 0;
        border-radius: 50%;
        z-index: -5;
    }
}

.slick-prev {
    background: white;
    position: absolute;
    top: 50%;
    margin-top: -32px;
    z-index: 10;
    left: -32px;

    &::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        background: url(/img/slider/arrowRight.png) center center no-repeat;
        top: 0;
        left: 0;
        border-radius: 50%;
        z-index: -5;
    }
}

.slider-blog {
    margin-top: 50px;

    .slick-prev {
        left: -32px;
    }

    .slick-next {
        right: -32px;
    }

    .slider-blog__item {
        width: 360px;
    }

    .slider-blog__item+.slider-blog__item {
        margin-left: 40px;
    }

    .slider-blog__title {
        margin-top: 25px;
        margin-bottom: 16px;
    }

    .slider-blog__desc {
        margin-bottom: 20px;
        margin-top: 0;
    }

    .button {
        width: 160px;
        display: flex;
        text-align: center;
        padding: 0;
        height: 50px;
        justify-content: center;
        align-items: center;
    }
}

.contacts-form {
    background: linear-gradient(90deg, #1F4E78 0%, #0677DF 100%);
    width: 100%;
    padding-top: 50px;
    padding-bottom: 105px;
    display: flex;
    flex-direction: column;
    align-items: center;

    &__title {
        font-family: 'Arimo';
        font-weight: bold;
        font-size: 30px;
        color: white;
        text-align: center;
    }

    &__subTitle {
        text-align: center;
        color: rgba(255, 255, 255, 0.7);
        font-family: 'Arimo';
        font-size: 18px;
        margin-top: 20px;
        margin-bottom: 30px;
    }

    .input__wrapper {

        input,
        textarea {
            padding: 15px 0;
            background: none;
            border-bottom: 2px solid #FFFFFF;
            font-size: 14px;
            font-family: 'Arimo';
            color: white;
            width: 360px;
            margin-bottom: 25px;

            &::placeholder {
                font-size: 14px;
                font-family: 'Arimo';
                color: rgba(255, 255, 255, 0.7);
            }
        }

        textarea {
            resize: none;
            height: 160px;
        }
    }
}

.send-form {
    background: linear-gradient(90deg, #0677DF 0%, #3CA2FE 100%);
    box-shadow: 0px 20px 50px rgba(14, 70, 120, 0.6);
    border-radius: 100px;
    width: 360px;
    height: 60px;
    font-family: 'Arimo';
    font-weight: bold;
    font-size: 14px;
    color: #FFFFFF;

}