.section__title {
    font-size: 72px;
    color: $textColor;
}

.section__desc {
    font-family: 'Arimo';
    color: rgba($color: $textColor, $alpha: .7);
    margin-top: 35px;
    font-size: 16px;
    line-height: 180%;

    span {
        color: $textColor;
    }
}

.section {
    margin-bottom: 150px;
}

.about__section,
.mission__section {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    .section__row {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .section__column {
        &.desc {
            max-width: 460px;
        }
    }

    .section__column+.section__column {
        margin-left: 40px;
    }
}

.about__section {
    .section__column {
        &:nth-child(1) {
            max-width: 460px;
        }

        &:nth-child(2) {
            flex-basis: 60%;
        }
    }
}

.mission__section {
    .section__column {
        &:nth-child(2) {
            max-width: 460px;
        }

        &:nth-child(1) {
            flex-basis: 65%;
        }
    }
}


.title-second {
    font-family: 'Arimo';
    font-weight: bold;
    font-size: 20px;
}

.formWhom__section {
    background: linear-gradient(180deg, #E3F0F0 0%, rgba(227, 240, 240, 0.5) 100%);
    padding-top: 80px;

    .section__title {
        margin-bottom: 110px;
        text-align: center;
    }

    .section__desc {
        color: rgba(255, 255, 255, 0.7);
        margin-bottom: 50px;
    }

    .section__row {
        display: flex;
        position: relative;
    }

    .section__column {
        flex-basis: 100%;
        position: relative;
        height: 750px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        &:nth-child(1) {
            align-items: flex-end;
            padding-right: 125px;
            position: relative;
            background: linear-gradient(90deg, #1F4E78 0%, #0677DF 100%);

            .formWhom__img {
                right: 0;
            }
        }

        &:nth-child(2) {
            padding-left: 125px;
        }
    }
}

.formWhom__desc {
    max-width: 350px;
    position: relative;
    z-index: 2;
    color: white;
}

.formWhom__img {
    position: absolute;
    bottom: 0;
    z-index: 0;
}

.formWhom__bckg {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(6, 41, 72, 0) 23.39%, #124B7D 100%);
}

.section__subTitle {
    font-size: 30px;
    line-height: 130%;
    color: $textColor;
    font-family: 'Arimo';
}

.slider-one {
    margin-top: 40px;

    .slider__title {
        font-family: 'Arimo';
        font-size: 20px;
        max-width: 250px;
        color: $textColor;
        margin-bottom: 20px;
    }

    .slider__desc {
        font-family: 'Arimo';
        font-size: 16px;
        color: rgba(33, 39, 45, 0.7);
    }

    .slider__row {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    .slider__column {
        &:nth-child(2) {
            max-width: 400px;
        }
    }

    .slider__column+.slider__column {
        margin-left: 100px;
    }
}

.professors__section {
    background: linear-gradient(180deg, #E3F0F0 0%, rgba(227, 240, 240, 0) 100%);
}

.patient__section {
    .column-img {
        height: 780px;
        background: linear-gradient(270deg, #145793 7.24%, rgba(0, 33, 62, 0.16) 82.72%), url(/img/slider/second.png) no-repeat;
    }

    .section__row {
        display: flex;
        position: relative;

        &::before {
            content: url(/img/dna-patient.png);
            position: absolute;
            right: 0;
        }
    }

    .section__column {
        &:nth-child(1) {
            flex-basis: 55%;
        }

        &:nth-child(2) {
            background: #145793;
            flex-basis: 45%;
            display: flex;
            align-items: flex-start;
            flex-direction: column;
            justify-content: center;

            .section__desc {
                max-width: 450px;
                margin-top: 45px;
                color: rgba(255, 255, 255, 0.7);
            }

            .section__title {
                color: white;
            }
        }
    }
}

.contacts__section {
    background: linear-gradient(180deg, #E3F0F0 0%, rgba(227, 240, 240, 0) 100%);
    padding-top: 150px;
    margin-bottom: 0;
    padding-bottom: 150px;
    .section__row {
        display: flex;
        align-items: center;

        .section__column {
            flex-basis: 50%;
        }
    }
}

.contacts__column {
    .section__title {
        font-size: 30px;
        font-family: 'Arimo';
    }

    .section__desc {
        font-size: 20px;
        line-height: 150%;
    }
}

.person-list {
    margin-top: 55px;
    margin-bottom: 80px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.person-list__info {
    margin: 0;
}

.person-list__title {
    margin-top: 15px;
    margin-bottom: 15px;
}

.link {
    font-size: 16px;
    line-height: 180%;
    font-family: 'Arimo';
    text-decoration-line: underline;
    color: #0677DE;
    font-weight: bold;
    margin-top: 25px;
    display: block;
}

.img-fluid {
    position: absolute;
    right: 0;
    top: -20px;
    z-index: 2;
    width: unset;
}

.about__section {
    position: relative;

    &::before {
        content: '';
        z-index: 1;
        position: absolute;
        width: 100%;
        display: block;
        height: 100%;
        background: url(/img/dna-welcome.png) top left no-repeat;
    }

    .section__row {
        height: 600px;
    }
}

.about__bckg {
    position: absolute;
    left: 0;
    bottom: 0;
    top: 0;
    z-index: 0;
}

.mission__section {
    .section__row {
        height: 600px;
    }

    .img-fluid {
        left: 0;
    }
}

.institutes__section {
    position: relative;
    overflow: hidden;
    height: 900px;
    margin-bottom: 0;

    &::before {
        position: absolute;
        left: 0;
        content: url(/img/section/institute.png);
        overflow: hidden;
        z-index: -1;
    }

    .section__title {
        color: white;
        margin-top: 120px;
    }
}

.institutes__slider {
    .img-slider {
        background: linear-gradient(88.67deg, #145793 12.24%, rgba(0, 0, 0, 0) 76.29%), url(/img/section/instituteSlider.png) no-repeat;
        height: 900px;
        position: absolute;
        top: 0;
        right: 0;
        width: 60%;
        z-index: -1;
    }

    .slider__row {
        display: flex;
    }

    .slider__column {
        flex-basis: 50%;
    }

    .slider__title {
        color: white;
        font-family: 'Arimo';
        font-size: 24px;
        margin-top: 120px;
        margin-bottom: 20px;
    }

    .slider__desc {
        font-family: 'Arimo';
        font-size: 16px;
        color: rgba(#FFFFFF, .7);
        max-width: 460px;
        line-height: 180%;
    }
}

.professors__section {
    padding-top: 150px;
    position: relative;
    &::before{
        content:'';
        position: absolute;
        width: 100%;
        height: 100%;
        background:url(/img/section/dna-certificer.png) no-repeat;
        left: 0;
        top: 250px;
    }
    .section__title {
        text-align: center;
        margin-bottom: 30px;
    }
}

.professors-slider {
    position: relative;
    &::before{
        content:'';
        position: absolute;
        width: 100%;
        height: 500px;
        bottom:0;
        left: 0;
        background: linear-gradient(90deg, #1F4E78 0%, #0677DF 100%);
    }
    .slick-next{
        right: -32px;
    }
}

.professorrs-slide__name {
    font-family: 'Arimo';
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 23px;
    margin-bottom: 15px;
    color: #FFFFFF;
}
.professorrs-slider__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .professorrs-slider__img{
        &::before{
            position: absolute;
                    content:'';
                    bottom:0;  
                    left: 0;
                    background:transparent;
                    width: 100%;
                    height: 100%;
                    z-index: 2;
                    transition: .3s all ease-in-out;
        }
    }
    &.slick-current{
        .professorrs-slider__img{
            position: relative;
            &::before{
                background: linear-gradient(180deg, rgba(6, 41, 72, 0) 23.39%, #124B7D 100%);
            }
        }
        .professorrs-slider__info{
            opacity: 1;
        }
    }
}

.professorrs-slider__info {
    position: absolute;
    z-index: 2;
    bottom: 40px;
    width: 250px;
    left: 50%;
    margin-left: -125px;
    opacity: 0;
    transition: 0.3s all ease-in-out;
}

.professors-slider__post {
    font-family: 'Arimo';
    font-size: 16px;
    color: rgba(255, 255, 255, 0.7);
}