.certificates__section{
    margin-top: 150px;
    position: relative;
   
}
.certificates__row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.certificates__item+.certificates__item {
    margin-left: 30px;
}
.certificates__item {
    background: #C4C4C4;
    box-shadow: 0px 30px 50px #DCEDED;
    border: 2px solid #CDCDCD;
    transition: .3s all ease-in-out;
    &:hover{
        margin-top: -20px;
    }
}
.certificates__section{   
    .section__title{
        margin-bottom: 86px;
        text-align: center;
    }
}