.header{
    height: 120px;
    .container{
        display: flex;
        align-items: center;
        height: inherit;
    }
}
.header__block{
    &.header-nav{
        margin-left: auto;
    }
    &:nth-child(4){
        margin-left: 40px;
    }
    .nav-list{
        display: flex;
    }
    .nav-list__item{
        &.active{
            a{
                color:$textColor;
            }
        }
        a{
            font-family: 'Arimo', ;
            font-size: 16px;
            color:$textColorLight;
        }
    }
    .nav-list__item + .nav-list__item{
        margin-left: 45px;
    }
}