.footer{
    background: #111E29;
    height: 180px;
    color: #8495A4;
    font-size: 16px;
    font-family:'Arimo';
    padding-top: 40px;
    .container{
        display: flex;
        justify-content: space-between;
    }
}
.footer__text{
    span{
        font-weight: bold;
    }
}
.footer-list{
    display: flex;
}
.footer-list__item + .footer-list__item{
    margin-left: 45px;
}