.burger {
    width: 40px;
    height: 22px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;

    &__item {
        width: 40px;
        display: block;
        height: 2px;
        background: #21272D;

        &:nth-child(3) {
            width: 20px;
        }
    }
}
.mobile{
    display: none;
}
@media screen and (max-width:1250px) {
   .slider-blog{
       .slick-next{
           right: 0;
       }
       .slick-prev{
           left: 0;
       }
   }
}
@media screen and (max-width:1190px){
    .slider-one{
        .slick-prev{
            left: 0;
        }
        .slick-next{
            right: 0;
        }
        .slider__row{
            flex-direction: column;
        }
        .slider__title{
            max-width:unset;
            margin-top: 20px;
        }
        .slider__column{
            flex-basis: unset;
            text-align: center;
            &:nth-child(2){
                max-width:unset
            }
        }
        .slider__column + .slider__column{
            margin-left: 0;
        }
    }
    .patient__section .section__row::before{
        display: none;
    }
    .workShop__section::before{
        display: none;
    }
    .patient__section{
        .column-img{
            background: linear-gradient(180.17deg, #145793 0.11%, rgba(0, 33, 62, 0.16) 125.64%), url(/img/slider/second.png);
        }
        .section__row{
            flex-direction: column-reverse;
        }
         .section__column:nth-child(1){
            flex-basis: unset;
        }
        .section__column:nth-child(2){
            flex-basis: unset;
            padding-top:80px;
        }
    }
    
    .img-fluid{
        position: relative;
        width: 100%;
    }
}
@media screen and (max-width:900px) {
    .header {
        padding: 0 30px;
        .descktop {
            display: none;
        }

        .mobile {
            display: block;
        }
    }
}
@media screen and (max-width:768px){
    .section__row {
        flex-direction: column;
    }
    .about__section,
    .mission__section {
        .section__column+.section__column {
            margin-left: 0;
        }
    }
    .mission__section{
        .section__row{
            flex-direction: column-reverse;
        }
        .section__column{
            &.desc{
                max-width: unset;
            }
        }
    }
    .section__desc,.section__title{
        padding-right: 20px;
        padding-left: 20px;
    }
    .section__desc{
        margin-bottom: 20px;
    }
}